@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --small-vp (width <= 767.9px);
@custom-media --not-small-vp (width >= 768px);
@custom-media --x2 (min-resolution: 2dppx);
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
}
:root {
  --accent-font: "Oswald", "Impact", sans-serif;
  --main-font: "Arial", "Helvetica", sans-serif;
}
:root {
  --white: #ffffff;
  --white-opacity: rgba(255, 255, 255, 0.3);
  --black: #000000;
  --black-light: #111111;
  --green: #68b738;
  --green_hover: #5eaa2f;
  --green_background: rgba(104, 183, 56, 0.85);
  --grey-dark-special: #222222;
  --grey-dark: #444444;
  --grey-special: #666666;
  --grey-basic_background: #f2f2f2;
  --grey_background: #eaeaea;
  --grey_hover: #ebebeb;
  --grey_active: rgba(68, 68, 68, 0.3);
  --color-cat_background: #444348;
  --color-cat-catalog_background: #1a1a1a;
  --transparent: rgba(0, 0, 0, 0);
  --color-grey_border: #e6e6e6;
  --color-form_border: #e7e7e7;
  --color-catalog_border: #ebebeb;
  --color-catalog-more: #d3d3d3;
  --color-grey-base_border: #d9d9d9;
  --color-grey-special_border: #cdcdcd;
  --color-decoration_underline: rgba(0, 0, 0, 0.24);
  --color-range_shadow: rgba(0, 0, 0, 0.16);
  --color-range_shadow-hover: rgba(0, 0, 0, 0.19);
  --color-range_shadow-active: rgba(0, 0, 0, 0.2);
  --color-error: #ff8282;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  height: 100%;
  font-family: var(--main-font);
}
.page__body {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-width: 320px;
  min-height: 100%;
  margin: 0;
  background-color: var(--white);
}
.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.page__wrapper-center {
  margin: 0 20px;

  @media (--middle-vp) {
    margin: 0 30px;
  }

  @media (--wide-vp) {
    width: 1220px;
    margin: 0 auto;
  }
}
.page-main {
  flex-grow: 1;
}
.page-main__selection {
  @media (--wide-vp) {
    margin-bottom: 82px;
  }
}
.page-main__advantages {
  @media (--wide-vp) {
    margin-bottom: 51px;
  }
}
.page-main__catalog-add {
  @media (--wide-vp) {
    margin-bottom: 86px;
  }
}
.button {
  font-family: var(--accent-font);
  font-weight: 400;
  width: fit-content;
  text-decoration: none;
  text-transform: uppercase;
  color: var(--white);
  background-color: var(--green);
  border: none;

  &:focus {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:hover {
    background-color: var(--green_hover);
  }

  &:active {
    background-color: var(--green_hover);
    color: var(--white-opacity);
  }

  &--size_wide {
    width: 100%;
  }
}
.page-header {
  position: relative;
  display: flex;
  flex-direction: column;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 2fr;
    padding: 15px 0;
  }

  @media (--wide-vp) {
    grid-template-columns: 1fr 1fr;
    padding: 20px 0;
  }
}
.page-header__wrapper-logo {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 17px;

  @media (--small-vp) {
    position: relative;

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      left: -20px;
      bottom: 0;
      background-color: var(--color-grey_border);
      box-shadow: 40px 0 0 0 var(--color-grey_border);
    }
  }
}
.page-header__logo-link {
  text-decoration: none;

  &:focus {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}
.page-header__logo-link--title {
  margin: 0 auto;
  padding-right: 33px;

  @media (--not-small-vp) {
    padding-right: 0;
    margin-left: 10px;
  }
}
.page-header__logo-image {
  width: 34px;
  height: 39px;

  @media (--not-small-vp) {
    width: 61px;
    height: 52px;
  }

  @media (--wide-vp) {
    width: 71px;
    height: 60px;
  }
}
.page-header__logo-title {
  width: 102px;
  height: 19px;

  @media (--not-small-vp) {
    --fill: var(--grey-dark);
  }

  @media (--wide-vp) {
    width: 118px;
    height: 22px;
  }
}
.page-header__main-nav {
  @media (--not-small-vp) {
    margin-right: -17.5px;
  }
}
.page-footer {
  background-color: var(--grey-basic_background);

  &--within_index {
    --contacts-color: var(--white);
  }
}
.page-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.page-footer__location {
  background-color: var(--grey_background);
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: auto;

  @media (--wide-vp) {
    grid-row: 1 / -1;
    grid-column: 1 /-1;
  }
}
.page-footer__iframe {
  display: block;
  border: none;
  width: 100%;
  height: 362px;
  grid-row: 1 / -1;
  grid-column: 1 /-1;

  @media (--not-small-vp) {
    height: 400px;
  }
}
.page-footer__noscript {
  grid-row: 1 / -1;
  grid-column: 1 /-1;
}
.page-footer__wrapper-grid {
  background-color: var(--contacts-color, var(--transparent));

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }
}
.page-footer__contacts {
  @media (--wide-vp) {
    margin-top: 98px;
    grid-row: 1 / -1;
    grid-column: 1 /-1;
    z-index: 2;
  }
}
.page-footer__logo-link {
  &:focus {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:focus:not(:focus-visible) {
    outline: none;
    outline-offset: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
    outline-offset: 2px;
  }

  &:hover {
    opacity: 80%;
  }

  &:active {
    opacity: 60%;
  }
}
.page-footer__logo-title {
  padding-top: 39px;
  padding-bottom: 32px;
}
.contacts {
  box-sizing: border-box;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 26px 20px;

  @media (--not-wide-vp) {
    background-color: var(--contacts-color, var(--transparent));
  }

  @media (--middle-vp) {
    padding: 56px 70px;
  }

  @media (--wide-vp) {
    width: 565px;
    height: 198px;
    padding: 56px 80px;
    background-color: var(--white);
  }
}
.contacts__cooperation {
  font-style: normal;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--black-light);
  text-transform: uppercase;
}
.contacts__text {
  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  color: var(--grey-dark);
}
.location__noscript-image {
  display: block;
  object-fit: cover;
  object-position: center;
  width: 100%;
  height: 362px;

  @media (--middle-vp) {
    height: 400px;
  }

  @media (--wide-vp) {
    max-width: 1440px;
    height: 400px;
    margin: 0 auto;
  }
}
.social {
  @media (--small-vp) {
    width: 100%;
    border-top: 1px solid var(--color-grey-base_border);
    border-bottom: 1px solid var(--color-grey-base_border);
  }
}
.social__list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  list-style-type: none;
  padding: 0;
  margin: 0;
}
.social__link {
  display: block;
  padding: 20px 15px;
  color: var(--grey-special);

  &:focus {
    outline: 2px solid var(--black);
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
  }

  &:hover {
    color: var(--green);
  }

  &:active {
    color: var(--green);
    opacity: 30%;
  }

  &::before {
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: currentColor;
    width: 22px;
    height: 22px;

    @media (--not-small-vp) {
      width: 34px;
      height: 34px;
    }
  }

  &--vkontakte::before {
    mask-image: url("../icons/sprite.svg#footer-icon-vkontakte");
  }

  &--instagram::before {
    mask-image: url("../icons/sprite.svg#footer-icon-instagram");
  }

  &--facebook::before {
    mask-image: url("../icons/sprite.svg#footer-icon-facebook");
  }
}
.copyright {
  font-size: 16px;
  line-height: 18px;
  align-self: flex-start;
  width: 100%;

  @media (--not-small-vp) {
    align-self: center;
  }
}
.copyright__link {
  position: relative;
  display: block;
  color: var(--grey-dark);
  text-decoration: none;
  padding-top: 28px;
  padding-bottom: 32px;
  --color-situation: var(--grey-special);

  @media (--not-small-vp) {
    width: fit-content;
    padding-right: 50px;
    margin-left: auto;
  }

  &:focus {
    outline: 2px solid var(--black);
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
  }

  &:hover {
    --color-situation: var(--green);
  }

  &:active {
    --color-situation: var(--green);
    opacity: 30%;
  }

  &::before {
    position: absolute;
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background: var(--color-situation);
    mask-image: url("../icons/sprite.svg#footer-logo-html");
    width: 27px;
    height: 34px;
    top: 22px;
    right: 0;
  }
}
.main-nav {
  font-family: var(--accent-font);
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;

  @media (--small-vp) {
    &--closed .main-nav__list {
      display: none;
    }

    &--opened .main-nav__list {
      position: absolute;
      z-index: 2;
      width: 100%;
    }

    &--nojs .main-nav__list {
      display: block;
    }
  }

  &--within_index {
    --link-color: var(--white);

    @media (--wide-vp) {
      position: relative;

      &::before {
        content: "";
        position: absolute;
        z-index: -2;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: var(--green_background);
      }
    }
  }
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;
  text-align: center;

  @media (--not-small-vp) {
    flex-direction: row;
    justify-content: space-between;
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

  @media (--wide-vp) {
    font-size: 20px;
    line-height: 30px;
    justify-content: flex-end;
  }
}
.main-nav__item {
  @media (--small-vp) {
    position: relative;
    background-color: var(--white);
    box-shadow: 20px 0 0 0 var(--white), -20px 0 0 0 var(--white);

    &::before {
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      left: -20px;
      bottom: 0;
      background-color: var(--color-grey_border);
      box-shadow: 40px 0 0 0 var(--color-grey_border);
    }
  }
}
.main-nav__link {
  display: block;
  color: var(--black);
  text-decoration: none;
  text-transform: uppercase;
  padding: 22px 25px 24px;

  @media (--not-small-vp) {
    padding: 22px 17.5px 24px;

    &[aria-current] {
      text-decoration: underline;
      text-decoration-thickness: 2px;
      text-underline-offset: 10px;
      text-decoration-color: var(--green);
    }
  }

  @media (--wide-vp) {
    color: var(--link-color, var(--black));

    &[aria-current] {
      text-decoration-color: var(--link-color, var(--green));
    }
  }

  &:focus {
    outline: 2px solid var(--black);
  }

  &:focus:not(:focus-visible) {
    outline: none;
  }

  &:focus-visible {
    outline: 2px solid var(--black);
  }

  &:hover {
    opacity: 60%;
  }

  &:active {
    opacity: 30%;
  }
}
.main-nav__toggle {
  position: absolute;
  top: 10px;
  right: 0;
  width: 34px;
  height: 38px;
  background-color: var(--white);
  cursor: pointer;
  border: none;

  @media (--small-vp) {
    display: block;
  }
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 12px;
  left: 5px;
  width: 24px;
  height: 1.5px;
  background-color: var(--black);
  box-shadow: 0 7px 0 0 var(--black), 0 14px 0 0 var(--black);
}
.main-nav--opened .main-nav__toggle {
  &::before,
  &::after {
    content: "";
    position: absolute;
    top: 18px;
    left: 5px;
    width: 25px;
    height: 1.5px;
    background-color: var(--black);
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.selection {
  display: flex;
  flex-direction: column;
  font-family: var(--accent-font);
  color: var(--white);
  --bg-url: url("../img/index-bg-cat-s@1x.jpg");


  @media (--small-vp) {
    position: relative;
    text-align: center;
  }

  &::before {
    position: absolute;
    content: "";
    background:
      linear-gradient(
        var(--green_background),
        var(--green_background)
        ),
      var(--bg-url);
    width: 100%;
    height: 302px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    background-color: var(--color-cat_background);
    z-index: -1;

    @media (--x2) {
      --bg-url: url("../img/index-bg-cat-s@2x.jpg");
    }

    @media (--middle-vp) {
      content: initial;
    }

    @media (--wide-vp) {
      right: 0;
      top: 0;
      width: 50%;
      height: 694px;
      --bg-url: url("../img/index-bg-cat-l@1x.jpg");
      background-position: bottom right;

      @media (--x2) {
        --bg-url: url("../img/index-bg-cat-l@2x.jpg");
      }
    }
  }
}
.selection__wrapper {
  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: auto;
  }

  @media (--wide-vp) {
    grid-template-columns: 1fr 1fr;
  }
}
.selection__title {
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  padding-top: 25px;
  margin-top: 0;
  margin-bottom: 25px;

  @media (--not-small-vp) {
    font-size: 60px;
    line-height: 60px;
    color: var(--black);
    grid-column: 1 / 2;
    grid-row: 1 / -1;
    margin-left: 87px;
  }

  @media (--wide-vp) {
    margin-top: 0;
    margin-left: 80px;
  }
}
.selection__slogan {
  font-style: normal;
  font-size: 14px;
  line-height: 14px;
  font-weight: 400;
  text-transform: uppercase;
  margin-top: 0;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 20px;
    color: var(--black);
    grid-column: 1 / 2;
    grid-row: 1 / -1;
    margin-top: 180px;
    margin-left: 87px;
  }

  @media (--wide-vp) {
    margin-top: 230px;
    margin-left: 80px;
  }
}
.selection__image-set {
  margin: 0 auto;

  @media (--not-small-vp) {
    grid-column: 1 / 2;
    grid-row: 1 / -1;
    z-index: -1;
    margin-top: 220px;
  }

  @media (--wide-vp) {
    grid-column: 2 / -1;
    margin-top: 0;
    margin-left: -150px;
  }
}
.selection__image {
  display: block;
  padding-top: 25px;
  margin: 0 auto;

  @media (--not-small-vp) {
    width: 709px;
    height: 609px;
  }

  @media (--wide-vp) {
    width: 552px;
    height: 499px;
  }
}
.selection__button-link {
  display: block;
  font-size: 14px;
  line-height: 18px;
  padding: 10px 0;
  width: 100%;
  margin-bottom: 20px;

  @media (--not-small-vp) {
    box-sizing: border-box;
    font-size: 20px;
    line-height: 20px;
    width: 244px;
    height: 54px;
    padding: 14px 26px;
    grid-column: 1 /-1;
    grid-row: 1 / -1;
    margin-top: 250px;
    margin-left: 87px;
  }

  @media (--wide-vp) {
    margin-top: 300px;
    margin-left: 80px;
  }
}
.advantages {
  color: var(--grey-dark);
  font-size: 14px;
  line-height: 18px;

  @media (--not-small-vp) {
    font-size: 16px;
    line-height: 24px;
    color: var(--black);
  }
}
.advantages__list {
  display: flex;
  flex-direction: column;
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.advantages__item {
  position: relative;
  background-color: var(--grey-basic_background);
  padding: 24px 20px 22px;

  &:not(:last-child) {
    margin-bottom: 20px;
  }

  &::before {
    position: absolute;
    content: "";
    width: 50px;
    height: 50px;
    background-color: var(--green);
    border-radius: 50%;

    @media (--middle-vp) {
      width: 200px;
      height: 200px;
      right: 60px;
      top: 92px;
    }

    @media (--wide-vp) {
      width: 100px;
      height: 100px;
      left: 52px;
      top: 47px;
    }
  }

  @media (--middle-vp) {
    padding: 77px 60px 57px 52px;
  }

  @media (--wide-vp) {
    padding: 47px 65px 59px 52px;

    &:not(:last-child) {
      margin-bottom: 0;
      margin-right: 20px;
    }
  }
}
.advantages__item--slim {
  &::after {
    position: absolute;
    content: "";
    background-image: url("../icons/sprite.svg#index-cat-slim");
    background-position: center;
    background-repeat: no-repeat;

    @media (--small-vp) {
      width: 50px;
      height: 50px;
      background-size: 50px;
      left: 22px;
      top: 21px;
    }

    @media (--middle-vp) {
      width: 200px;
      height: 200px;
      background-size: 200px;
      right: 61px;
      top: 81px;
    }

    @media (--wide-vp) {
      width: 100px;
      height: 100px;
      background-size: 100px;
      left: 52px;
      top: 41px;
    }
  }
}
.advantages__item--pro {
  &::after {
    position: absolute;
    content: "";
    background-image: url("../icons/sprite.svg#index-cat-pro");
    background-position: center;
    background-repeat: no-repeat;

    @media (--small-vp) {
      width: 68px;
      height: 68px;
      background-size: 68px;
      left: 13px;
      top: 15px;
    }

    @media (--middle-vp) {
      width: 268px;
      height: 268px;
      background-size: 268px;
      right: 28px;
      top: 57px;
    }

    @media (--wide-vp) {
      width: 134px;
      height: 134px;
      background-size: 134px;
      left: 33px;
      top: 34px;
    }
  }
}
.advantages__title {
  display: block;
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 37px;
  font-weight: 400;
  color: var(--black);
  text-transform: uppercase;
  margin-top: 10px;
