  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
    height: 52px;
    padding: 0 20px;
    margin-left: 15px;
  }

  &:focus {
    outline: 2px solid var(--grey-dark);
    outline-offset: -2px;
  }

  &:focus:not(:focus-visible) {
    outline: revert;
  }

  &:focus-visible {
    outline: 2px solid var(--grey-dark);
    outline-offset: -2px;
  }

  &:hover {
    outline: 1px solid var(--color-grey-special_border);
    outline-offset: -1px;
    --color-icon: var(--green);
  }

  &:hover + .form-owner__icon::before {
    --color-icon: var(--green);
  }

  &:not(:placeholder-shown) {
    outline: none;
  }

  &:disabled {
    cursor: not-allowed;
  }

  &--error {
    outline: 2px solid var(--color-error);
    outline-offset: -2px;
  }

  &--error + .form-owner__icon::before {
    --color-icon: var(--color-error);
  }
}
.form-owner__icon {
  &::before {
    content: "";
    display: block;
    mask-repeat: no-repeat;
    mask-position: center;
    background-color: var(--color-icon);
    width: 22px;
    height: 22px;
    margin-left: -36px;
  }

  &--email::before {
    mask-image: url("../icons/sprite.svg#form-icon-email");
  }

  &--phone::before {
    mask-image: url("../icons/sprite.svg#form-icon-phone");
  }
}
.form-commentary {
  @media (--wide-vp) {
    grid-column: 1 / -1;
  }
}
.form-commentary__label {
  position: relative;
  display: block;
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  margin-bottom: 38px;
  width: fit-content;
  --air-gap: 20px;

  &::before {
    position: absolute;
    content: "";
    display: block;
    height: 2px;
    top: 50%;
    background-color: var(--green);
    width: calc(100vw - 100% - var(--air-gap));
    right: calc(100% + var(--air-gap));
    box-shadow: calc(100vw + var(--air-gap)) 0 0 0 var(--green);
  }

  @media (--not-small-vp) {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 76px;
    --air-gap: 40px;
  }
}
.form-commentary__field {
  box-sizing: border-box;
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 18px;
  font-weight: 400;
  color: var(--grey-dark);
  text-transform: uppercase;
  width: 100%;
  min-height: 150px;
  padding: 9px 12px 9px 15px;
  border: 1px solid var(--color-form_border);

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 30px;
    min-height: 174px;
    padding: 10px 20px;
  }
}
.form-checkbox {
  border: none;

  @media (--wide-vp) {
    grid-column: 1 / -1;
  }
}
.form-checkbox__legend {
  position: relative;
  display: block;
  font-size: 24px;
  line-height: 26px;
  color: var(--black);
  padding: 0;
  margin-bottom: 34px;
  width: fit-content;
  --air-gap: 20px;

  &::before {
    position: absolute;
    content: "";
    display: block;
    height: 2px;
    top: 50%;
    background-color: var(--green);
    width: calc(100vw - 100% - var(--air-gap));
    right: calc(100% + var(--air-gap));
    box-shadow: calc(100vw + var(--air-gap)) 0 0 0 var(--green);
  }

  @media (--not-small-vp) {
    font-size: 36px;
    line-height: 36px;
    margin-bottom: 57px;
    --air-gap: 40px;
  }
}
.form-checkbox__list {
  display: flex;
  flex-direction: column;
  font-size: 16px;
  line-height: 18px;
  color: var(--black);
  text-transform: uppercase;
  list-style-type: none;
  margin: 0 0 34px;
  padding: 0;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    font-size: 20px;
    line-height: 30px;
    margin-bottom: 22px;
  }

  @media (--wide-vp) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 80px;
    margin-bottom: 29px;
  }
}
.form-checkbox__item {
  @media (--small-vp) {
    &:not(:last-child) {
      margin-bottom: 12px;
    }
  }

  @media (--not-small-vp) {
    margin-bottom: 32px;
  }
}
.form-checkbox__label {
  display: flex;
}
.form-checkbox__control-input {
  display: grid;
  position: relative;
  margin: 0;
  appearance: none;
  place-items: center;

  &::before {
    display: block;
    box-sizing: border-box;
    content: "";
    width: 30px;
    height: 30px;
    background-color: var(--white);
    border: 1px solid var(--color-grey-special_border);

    @media (--not-small-vp) {
      width: 40px;
      height: 40px;
    }
  }

  &:checked::after {
    position: absolute;
    display: block;
    content: "";
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../icons/sprite.svg#form-icon-tick");
    width: 26px;
    height: 26px;
  }
}
.form-checkbox__control-label {
  display: block;
  margin-left: 15px;
  align-self: center;

  @media (--not-small-vp) {
    margin-left: 20px;
  }
}
.catalog-main__title {
  position: relative;
  font-family: var(--accent-font);
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;
  color: var(--black);
  padding-top: 27px;
  margin: 0;
  padding-bottom: 40px;

  @media (--small-vp) {
    &::before {
      display: block;
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      left: 0;
      bottom: 0;
      background-color: var(--color-grey_border);
      box-shadow: -20px 0 0 0 var(--color-grey_border), 20px 0 0 0 var(--color-grey_border);
    }
  }

  @media (--not-small-vp) {
    font-size: 60px;
    line-height: 60px;
  }
}
.catalog__list {
  display: flex;
  flex-direction: column;
  list-style-type: none;
  margin: 0 0 25px;
  padding: 0;

  @media (--not-small-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 60px;
    margin-bottom: 68px;
  }

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 80px;
    row-gap: 60px;
    margin-bottom: 75px;
  }
}
.product {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 20px 0 25px;
  --top-indent: 84px;

  @media (--small-vp) {
    &::before {
      display: block;
      content: "";
      position: absolute;
      height: 1px;
      width: 100%;
      left: 0;
      bottom: 0;
      background-color: var(--color-grey_border);
      box-shadow: -20px 0 0 0 var(--color-grey_border), 20px 0 0 0 var(--color-grey_border);
    }
  }

  @media (--not-small-vp) {
    padding: 0 38px 40px;

    &:not(:last-child) {
      background-image: linear-gradient(to bottom, var(--white) var(--top-indent), var(--grey-basic_background) var(--top-indent));
    }
  }

  &--more {
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    @media (--not-small-vp) {
      text-align: center;
      background-color: var(--white);
      border: 2px solid var(--grey-basic_background);
      margin-top: var(--top-indent);
    }
  }
}
.product__link {
  text-decoration: none;

  &--title {
    display: block;
    grid-column: 2 / -1;
    width: 60%;
    margin-bottom: 14px;

    @media (--not-small-vp) {
      grid-column: 1 / -1;
      place-self: center;
      text-align: center;
    }

    @media (--wide-vp) {
      width: 80%;
    }
  }

  &--image {
    display: block;
    grid-column: 1 / -2;
    grid-row: 1 / 3;
    place-self: center;
    text-align: center;

    @media (--not-small-vp) {
      grid-column: 1 / -1;
      order: -1;
      min-height: 245px;
    }
  }

  &--more {
    grid-column: 1 /-1;
    margin: 0 auto;

    @media (--middle-vp) {
      width: 60%;
    }
  }
}
.product__image {
  display: block;
  object-position: center;
  object-fit: contain;

  @media (--not-small-vp) {
    width: auto;
    height: 240px;
  }
}
.product__title {
  font-family: var(--accent-font);
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--grey-dark-special);
  text-transform: uppercase;
  margin: 0;

  @media (--not-small-vp) {
    font-size: 20px;
    line-height: 24px;
  }

  &--more {
    position: relative;
    color: var(--black-light);
    margin-top: 58px;
    margin-bottom: 8px;

    &::before,
    &::after {
      content: "";
      position: absolute;
      background-color: var(--color-catalog-more);
      width: 30px;
      height: 2px;
      align-self: center;
      background-repeat: no-repeat;
      bottom: 60px;
      left: 50%;
      margin-left: -15px;

      @media (--not-small-vp) {
        width: 60px;
        bottom: 120px;
        margin-left: -30px;
      }
    }

    &::before {
      transform: rotate(90deg);
    }

    @media (--not-small-vp) {
      line-height: 22px;
      margin-top: 160px;
      margin-bottom: 23px;
    }
  }
}
.product__specification {
  grid-column: 2 / -1;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin-top: 0;
  margin-bottom: 10px;

  @media (--not-small-vp) {
    grid-column: 1 / -1;
    font-size: 16px;
    margin-bottom: 33px;
  }
}
.product__specification-set {
  display: flex;
  justify-content: space-between;

  @media (--not-small-vp) {
    border-bottom: 1px solid var(--color-grey-special_border);
    padding-top: 7px;
    padding-bottom: 9px;
  }
}
.product__definition {
  &--price {
    text-transform: uppercase;
  }
}
.product__description {
  grid-column: 1 /-1;
  font-size: 12px;
  line-height: 16px;
  font-weight: 400;
  color: var(--grey-dark);
  margin: 0 auto 21px;

  @media (--middle-vp) {
    width: 80%;
  }

  @media (--not-small-vp) {
    font-size: 16px;
    line-height: 18px;
    margin-bottom: auto;
  }

  @media (--wide-vp) {
    min-width: 167px;
  }
}
.product__button {
  box-sizing: border-box;
  display: grid;
  grid-column: 1 / -1;
  font-size: 16px;
  line-height: 20px;
  width: 100%;
  place-items: center;
  padding: 10px 35px;

  @media (--small-vp) {
    margin-top: 13px;
  }

  @media (--not-small-vp) {
    grid-column: 1 / -1;
  }

  &--more_color {
    color: var(--grey-dark);
    background-color: var(--grey-basic_background);

    &:hover {
      background-color: var(--grey_hover);
    }

    &:active {
      background-color: var(--grey_hover);
      color: var(--grey_active);
    }
  }
}
.catalog-add {
  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    column-gap: 79px;
  }
}
.catalog-add__card {
  &:not(:last-child) {
    margin-bottom: 13px;

    @media (--middle-vp) {
      margin-bottom: 25px;
    }

    @media (--wide-vp) {
      margin-bottom: 15px;
    }
  }
}
.catalog-add__list {
  display: flex;
  flex-direction: column;
  margin-top: 0;
  margin-bottom: 35px;
  padding-top: 13px;
  border-top: 1px solid var(--color-grey-special_border);
  border-bottom: 1px solid var(--color-grey-special_border);

  @media (--middle-vp) {
    padding-top: 25px;
    margin-bottom: 70px;
    border: 1px solid var(--color-grey-special_border);
  }

  @media (--wide-vp) {
    grid-column: 1 / -2;
    margin-bottom: 0;
  }
}
.catalog-add__title {
  display: block;
  position: relative;
  font-family: var(--accent-font);
  font-size: 24px;
  line-height: 24px;
  font-weight: 400;
  color: var(--black);
  width: fit-content;
  margin-top: 0;
  margin-bottom: 36px;
  --air-gap: 20px;

  @media (--not-small-vp) {
    font-size: 36px;
    line-height: 36px;
    --air-gap: 40px;
  }

  @media (--middle-vp) {
    margin-left: 38px;
  }

  @media (--wide-vp) {
    grid-column: 1 / -1;
  }

  &::before {
    position: absolute;
    content: "";
    display: block;
    height: 2px;
    top: 50%;
    background-color: var(--green);
    width: calc(100vw - 100% - var(--air-gap));
    right: calc(100% + var(--air-gap));
    box-shadow: calc(100vw + var(--air-gap)) 0 0 0 var(--green);
  }
}
.catalog-add__aside {
  margin: 20px 0;

  @media (--not-small-vp) {
    margin: 0;
  }
}
.product-add {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: min-content auto min-content;
  padding-bottom: 20px;

  &:not(:last-child) {
    border-bottom: 1px solid var(--color-grey-special_border);
  }

  @media (--middle-vp) {
    padding-left: 38px;
    padding-right: 38px;
    padding-bottom: 28px;
  }

  @media (--not-small-vp) {
    grid-template-columns: 1fr 1fr 1fr auto;
    grid-template-rows: auto min-content;
