  margin-left: 0;
  grid-column: 1/-1;
  grid-row: -1/-2;
}
@media (--middle-vp) {
  .product-add__order {
    margin-top: 25px;
  }
}
@media (--wide-vp) {
  .product-add__order {
    margin-top: 0;
    grid-column: -1/-2;
    grid-row: 1/2;
  }
}

.product-add__button {
  display: grid;
  box-sizing: border-box;
  width: 100%;
  place-items: center;
  padding: 10px 35px;
}

.catalog-aside {
  position: relative;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  color: var(--white);
  --bg-url: url("../img/catalog-bg-cat-s@1x.jpg");
  background: linear-gradient(var(--green_background), var(--green_background)), var(--bg-url) no-repeat;
  background-size: cover;
  background-position: center;
  width: 100%;
  height: 290px;
  background-color: var(--color-cat-catalog_background);
  text-align: center;
}
@media (--x2) {
  .catalog-aside {
    --bg-url: url("../img/catalog-bg-cat-s@2x.jpg");
  }
}
.catalog-aside::before {
  position: absolute;
  content: "";
  display: block;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url("../icons/sprite.svg#catalog-icon-gift");
  width: 78px;
  height: 80px;
  top: 60px;
  left: 50%;
  margin-left: -39px;
}
@media (--middle-vp) {
  .catalog-aside::before {
    top: 50%;
    margin-top: -40px;
    left: 116px;
  }
}
@media (--middle-vp) {
  .catalog-aside {
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 200px;
    text-align: left;
    --bg-url: url("../img/catalog-bg-cat-m@1x.jpg");
  }
}
@media (--middle-vp) and (--x2) {
  .catalog-aside {
    --bg-url: url("../img/catalog-bg-cat-m@2x.jpg");
  }
}
@media (--wide-vp) {
  .catalog-aside {
    height: 100%;
    --bg-url: url("../img/catalog-bg-cat-l@1x.jpg");
  }
}
@media (--wide-vp) and (--x2) {
  .catalog-aside {
    --bg-url: url("../img/catalog-bg-cat-l@2x.jpg");
  }
}

.catalog-aside__description {
  padding-top: 187px;
  margin: 0;
}
@media (--middle-vp) {
  .catalog-aside__description {
    margin-right: 124px;
    padding: 0;
  }
}